.sidebar{
    flex: 1;
    border-right: 0.5px solid rgb(228, 225, 225); 
    min-height: 100vh;
    background-color: white;


    .top{
        height: 50px;
        display: flex;
        align-items: center;
        justify-content: center ;
        background-color: black;

        .logo{
            font-size: 20px;
            font-weight: bold;
            color: red;
            background-color: black;
       
        }
    }

    hr{
        height: 0;
        border: 0.5px solid rgb(228, 225, 225); 
    }

    .center{
        padding-top: 35px;
        padding-left: 10px;
        width: 300px;
        border: 1px solid lightgray;
        height: 100vh;

        ul{
            list-style: none;
            margin: 0;
            padding: 0;

            .title{
                font-size: 10px;
                font-weight: bold;
                color: #999;
                margin-top: 15px;
                margin-bottom: 5px;
                                 
            }
            li{
                display: flex;
                align-items: center;
                padding: 5px;
                cursor: pointer;
                color: black;

                &:hover{
                    background-color: rgb(243, 87, 87);
                }

                .icon{
                    font-size: 18px;
                    color: rgb(243, 87, 87);;
                    text-align: center;
                }

                span{
                    font-size: 13px;
                    font-weight: 600;
                    color: #888;
                    margin-left: 10px;
                }
            }
        }
    }
}